.page-about {
  .site-header {
    height: 0px;
    width: 100vw;
  }
  .content-area {
    padding: 0;
    margin: 0;
  }
  .entry-header {
    display: none;
  }
  h2 {
    text-align: center;
  }
  .entry-content {
    padding: 0 0 3rem 0;
    margin: 0;
    :not(.hero-image-header) {
      margin-left: auto;
      margin-right: auto;
      max-width: 900px;
    }
    .hero-image-header {
      height: 100vh;
      margin-bottom: 3rem;
      h1 {
        color: #fff;
        font-size: 3rem;
      }
    }
    p {
      padding: 0 3rem;
    }
  }
}
